import { MouseEventHandler, PropsWithChildren } from "react"
import { RPThemeProps } from "@rp/react/theme"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { Typography } from "@rp/react/web/mui/core"

type PriceCellProps = PropsWithChildren<{
  tooltip?: string | React.ReactNode
  onClick?: MouseEventHandler<HTMLSpanElement>
  hoverBackgroundColor?: (theme: RPThemeProps) => string | undefined
  color?: (theme: RPThemeProps) => string
}>

function WatchlistDataTableClickableCell({
  children,
  tooltip,
  onClick,
  color,
  hoverBackgroundColor,
}: PriceCellProps) {
  return (
    <RPTooltip
      title={tooltip}
      placement="top"
      slotProps={{
        tooltip: { style: { maxWidth: 315 } },
      }}
    >
      <Typography
        component="span"
        onClick={ev => {
          ev.stopPropagation()
          onClick?.(ev)
        }}
        sx={theme => ({
          color,
          display: "inline-block",
          cursor: "pointer",
          px: 1,
          width: "fit-content",
          borderRadius: "8px",
          height: "fit-content !important",
          "&:hover": {
            backgroundColor: () => hoverBackgroundColor?.(theme),
          },
        })}
      >
        {children}
      </Typography>
    </RPTooltip>
  )
}

export { WatchlistDataTableClickableCell }
